@mixin displayFont ($sizeValue) {
    font-size: ($sizeValue/20) * 1rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    color:$white;
    line-height: 0.85;
}

@mixin subtitles ($sizeValue) {
    font-size: ($sizeValue/20) * 1rem;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    letter-spacing: 5px;
    line-height: 1;
}

@mixin bodyFont ($sizeValue) {
    font-size: ($sizeValue/20) * 1rem;
    font-family: 'Roboto Condensed', sans-serif;
    line-height: 1.3;
}

// General Styles 
html {
    font-size: 125%;
}

h1, 
h2,
h3,
footer p {
    margin:0;
    padding:0;
}

h2 {
    @include displayFont(100);
}

h3 {
    @include subtitles(30);
    font-weight: 400;
}

.subtitle {
    @include subtitles(20);
    padding:5px 0;
}

a{
    text-decoration: none;
}

button {
    @include bodyFont(24);
}

p{
    @include bodyFont(20);
    margin: 10px 0;
    padding:0;
}

span {
    @include subtitles(20);
    color:$black;
    letter-spacing: 1px;
}

// Header Styles

h1{
    @include displayFont(140);
}

input[type=text] {
    @include bodyFont(24);
    line-height: 1;
    &::placeholder {
        text-align: left;
    }
}  

.header-reset p {
    @include subtitles(25);
    text-align: center;
}

// Recently Viewed Section
.artist-name {
    @include displayFont (30);
    color:$white;
    text-transform: uppercase;
}

// Footer
footer p {
    @include bodyFont(18);
    text-align: center;
}

// Media Queries
@media (max-width:1100px) {
    h1 {
        @include displayFont(100);
    }
    h2 {
        @include displayFont(80);
    }
    h3 {
        @include subtitles(24);
    }
    input[type=text] {
        @include bodyFont(20);
    }  
}

@media (max-width:600px) {
    h1 {
        @include displayFont(80);
    }
    h2 {
        @include displayFont(65);
        padding:15px 0;
    }
    .top-tracks h2 {
        @include displayFont(40);
    }
}

@media (max-width:450px) {
    h1 {
        @include displayFont(60);
    }
    .subtitle {
        padding:0;
    }
    .error-message {
        @include bodyFont(18);
    }
}

